@import url(https://fonts.googleapis.com/css?family=Nanum+Gothic:400,700,800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Viga&display=swap);
@import url(https://fonts.googleapis.com/css?family=Nanum+Gothic:400,700,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.contents {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Nanum Gothic", sans-serif;
}
.search {
  box-sizing: border-box;
  display: flex;
  width: 470px;
  height: 45px;
  border: 2px solid #03cf5d;
  background: white;
  margin-bottom: 80px;
}
.inputData {
  flex: 1 1;
  margin-left: 10px;
  border: 0;
  outline: none;
  font-size: 18px;
  font-weight: bold;
}

.button {
  display: flex;
  justify-content: center;
  width: 50px;
  outline: none;
  border: 0;
  background: #03cf5d;
  cursor: pointer;
  color: white;
  font-weight: bold;
  font-size: 30px;
}

.dateInfo {
  padding-left: 7%;
  font-size: 18px;
  align-self: flex-start;
}

.movies {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-family: "Nanum Gothic", sans-serif;
}

.movie {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  width: 250px;
  margin: 0px 3% 20px;
}
.rank {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-align: center;
  width: 44px;
  height: 40px;
  background: #03cf5d;
  color: white;
  opacity: 0.8;
  position: relative;
  top: 22px;
  left: -20px;
}

.poster {
  border: 1px solid #d9d9d9;
  height: 300px;
}

.title {
  margin-top: 0;
  font-size: 25px;
  font-weight: bold;
}

.header {
  text-align: center;
}

.homeTitle {
  font-size: 65px;
  font-family: "Viga", sans-serif;
  letter-spacing: 5px;
  margin: 30px;
}

.homeLink {
  text-decoration: none;
  color: #03cf5d;
}

.loginButton {
  position: relative;
  right: -100%;
  padding: 7px;
  box-sizing: border-box;
  font-size: 17px;
  font-family: "Nanum Gothic", sans-serif;
  font-weight: 800;
  background: #03cf5d;
  border: 3px solid #03cf5d;
  border-radius: 20px;
}

.loginLink {
  text-decoration: none;
  color: white;
}

